import { Component, OnInit, ViewChild } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { GlobalService } from '../../../../shared-ui/global.service';
import { ModalDirective } from "ngx-bootstrap/modal";
import { currentUser } from '../../../../layouts/home-layout/user.model';
import { JwtService } from '../../../../shared-ui/jwt.service';
import { CompanySearch } from '../../../../shared-ui/models/global.model';
import { environment } from '../../../../../environments/environment';
import { Router } from '@angular/router';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

  @ViewChild("deleteCompanyModal", { static: false })
  public deleteCompanyModal: ModalDirective;
  currentUser: currentUser = new currentUser;
  
  pager: any = {};
  companies: any;
  pagination: any;
  multiSelect: any;
  selectedCompany: any = [];
  currentCompany: any;
  companyDeleteOption: string = 'single';
  companySearch: CompanySearch = new CompanySearch;
  loadingData: boolean = false;
  loadingCount: number = 1;
  order: string = 'companyName';
  reverse: boolean = true;

  constructor(
    private jwtService: JwtService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private globalService: GlobalService,
    private router: Router,
  ) {
    this.globalService.setLoadingLabel('Loading... Please Wait.');
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
    if(!this.currentUser['isSuccessHackerAdmin']){
      this.router.navigate(['/user/list']);
      return;
    }
    this.globalService.sendActionChildToParent('companies');
    this.companySearch.user = this.currentUser.id;
  }

  setOrder(value: string) {
    this.order = value;
    if (this.order === value) {
      this.companySearch.sortOrder = this.reverse ? "DESC" : "ASC";
      this.companySearch.sort = value;
      this.reverse = !this.reverse;
      this.getCompanies();
    }
  }

  ngOnInit() {
    this.getCompanies();
  }

  setCompanyObject(company: any){
    this.globalService.setCompanyObject(company);
  }

  getCompanies(){
    if(this.loadingCount < 2){
      this.loadingData = true;
    }
    this.spinner.show();
    let postObject = {
      page: this.companySearch.page,
      limit: this.companySearch.limit,
      user: this.companySearch.user,
      sort: this.companySearch.sort,
      sortOrder: this.companySearch.sortOrder,
    }
    if(this.companySearch.isActive){
      postObject['isActive'] = Number(this.companySearch.isActive);
    }
    this.globalService.getCompanies(this.companySearch).subscribe(data => {
      this.loadingCount++;
      this.spinner.hide();
      if(Object.keys(data).length){
        this.companies = data.items;
        this.pagination = data.meta.pagination;
      }
      this.loadingData = false;
    }, error => {
      this.spinner.hide();
      this.loadingData = false;
      if(error &&  error.error && error.error.message){
        this.toastr.error(error.error.message, 'Error '+error.status );
      }else{
        this.toastr.error( environment.reponseCode[error.status] , 'Error '+error.status );
      }
    });
  }

  showDeleteModal(type: string, company: any){
    this.companyDeleteOption = type;
    this.currentCompany = company;
    this.deleteCompanyModal.show();
  }

  multiSelectCompany(multiSelect: any){
    this.selectedCompany = [];
    this.companies.forEach(element => {
      if(multiSelect){
        element.added = false;
      }else{
        element.added = true;
        this.selectedCompany.push(element);
      }
    });
  }
  
  selectCompany(company: any){
    let found = this.selectedCompany.filter(e => e.companyId == company['companyId']);
    if(found.length){
      this.selectedCompany.splice(this.selectedCompany.indexOf(found[0]), 1);
    }else{
      this.selectedCompany.push(company);
    }
  }

  deleteCompany(){
    let postData = {
      name: this.currentCompany.companyName,
      isActive: 0,
      modifier: this.currentUser.id,
    }
    this.globalService.setLoadingLabel('Updating... Please Wait.');
    this.spinner.show();
    this.globalService.updateCompany(postData, this.currentCompany['companyId']).subscribe(data => {
      this.spinner.hide();
      if(data && data.modifiedBy){
        this.toastr.success( "Data updated successfully.", "Success");
        this.currentCompany.isActive = data.isActive;
      }else{
        this.toastr.error("There are some error while updating the data!", "Error");
      }
      this.deleteCompanyModal.hide();
    }, error => {
      this.spinner.hide();
      this.deleteCompanyModal.hide();
      this.toastr.error(error.error.message, 'Error '+error.status );
    });
  }
  
  deleteAllCompany(){
    this.globalService.setLoadingLabel('Updating... Please Wait.');
    this.spinner.show();
    this.selectedCompany.forEach((element: any, index: Number) => {
      let postData = {
        name: element.companyName,
        isActive: 0,
        modifier: this.currentUser.id,
      }
      this.globalService.updateCompany(postData, element['companyId']).subscribe(data => {
        if(index == this.selectedCompany.length-1){
          this.spinner.hide();
          this.deleteCompanyModal.hide();
          this.getCompanies();
          this.toastr.success( "Data updated successfully.", "Success");
        }
      });
    }, error => {
      this.spinner.hide();
      this.deleteCompanyModal.hide();
      this.toastr.error(error.error.message, 'Error '+error.status );
    });
  }

  clearFilter(){
    this.companySearch.isActive = "";
    this.companySearch.page = 1;
    this.companySearch.limit = 25;
    this.companySearch.sort = "companyName";
    this.companySearch.sortOrder = 'ASC';
    this.reverse = true;
    this.getCompanies();
  }
  
}
